* {
  padding: 0;
  margin: 0;
}
.fixed {
  position: sticky;
  top: 0;
  z-index: 100;
}

.header__container {
  background-color: black;
  display: flex;
  gap: 10px;
  align-items: center;
  color: white;
  height: 70px;
}
.logo__container {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 200px;
}
.logo__container img {
  width: 80%;
  padding-top: 5px;
  margin-left: 20px;
  vertical-align: middle;
}
.delivery {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3px;
  font-weight: bold;
  padding: 5px;
  font-family: Arial, Helvetica, sans-serif;
}
.delivery p {
  font-size: 10px;
}
.search {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 55px;
  overflow: hidden;
  width: 100%;
  height: 40px;
  flex: 1;
}
.search select,
.search input
 {
  
  
  font-weight: bold;
  outline: 3px solid white;
}
.search select {
  border: none;
  
  padding: 11px;
  background-color: white;
}
.search input {
  margin-top: 16px;
  border: 1px solid white;
  width: 100%;
  margin-right: -2px;
  
}

.search svg {
  padding: 12px;
  background: #febd69;
  color: #1a1a1a;
  size: 42px;
}
.search svg:hover {
  background-color: #d49644;
}
.search:focus-within {
  border: 2px solid rgb(235, 161, 173);
}
.order__container {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 450px;
  gap: 10px;
}
.order__container a {
  text-decoration: none;
  color: white;
}
.order__container p {
  font-size: 12px;
  font-family: Arial, Helvetica, sans-serif;
}
.language {
  width: 80px;
  display: flex;
  padding: 10px;
}
.language select {
  background-color: #1a1a1a;
  border: none;
  color: white;
}
.language img {
  width: 40%;
}
.cart {
  position: relative;
  margin-left: 20px;
  width: 50px;
  font-size: 35px;
}

.cart span {
  position: absolute;
  font-weight: bold;
  top: -1px;
  font-size: 18px;

  left: 14px;
  color: orange;
}
.order__container a:hover,
.delivery:hover,
.logo__container a:hover,
.lower__container ul li:hover {
  border: 1px solid white;
  border-radius: 3px;
}
.lower__container {
  background-color: #232f3e;
  color: white;
}
.lower__container ul {
  display: flex;
  list-style: none;
  gap: 5px;
}
.lower__container ul li {
  padding: 8px;
  font-family: Arial, sans-serif;
  font-size: 15px;
}
.lower__container ul li:first-child {
  display: flex;
  gap: 2px;
  align-items: center;
}
@media screen and (max-width: 576px) {
  .header__container {
    display: flex;
    flex-direction: column !important;
    height: auto;

  }
  .lower__container ul li:not(:first-child) {
    display: none;
  }
  .language {
    display: none;
  }
  .search {
    
    width: 100%;
  }
  
}
